/* Some stylesheet reset */
#cssmenu[data-v-1ba87392] {
  position: fixed;
  top: 7%;
  margin-top: -2.5em;
  left: 5px;
}
#cssmenu > ul[data-v-1ba87392] {
  list-style: none;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  line-height: 1;
}
/* The container */
#cssmenu > ul[data-v-1ba87392] {
  display: block;
  position: relative;
  width: 150px;
  /* The list elements which contain the links */
}
#cssmenu > ul li[data-v-1ba87392] {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
}
/* General link styling */
#cssmenu > ul li a[data-v-1ba87392] {
  /* Layout */

  display: block;
  position: relative;
  margin: 0;
  border-top: 1px dotted #3a3a3a;
  border-bottom: 1px dotted #1b1b1b;
  padding: 11px 20px;
  /* Typography */

  font-family: Helvetica, Arial, sans-serif;
  color: #d8d8d8;
  text-decoration: none;
  text-transform: uppercase;
  text-shadow: 0 1px 1px #000;
  font-size: 13px;
  font-weight: 300;
  /* Background & effects */

  background: #1f5375;
}
/* Rounded corners for the first link of the menu/submenus */
#cssmenu > ul li:first-child > a[data-v-1ba87392] {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-top: 0;
}
/* Rounded corners for the last link of the menu/submenus */
#cssmenu > ul li:last-child > a[data-v-1ba87392] {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom: 0;
}
/* The hover state of the menu/submenu links */
#cssmenu > ul li > a[data-v-1ba87392]:hover,
#cssmenu > ul li:hover > a[data-v-1ba87392] {
  color: #ffffff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
  background: #54cdf1;
  background: -webkit-gradient(linear, left top, left bottom, from(#72d4f2), to(#54cdf1));
  background: linear-gradient(#72d4f2, #54cdf1);
  border-color: transparent;
}
/* The arrow indicating a submenu */
#cssmenu > ul .has-sub > a[data-v-1ba87392]::after {
  content: "";
  position: absolute;
  top: 16px;
  right: 10px;
  width: 0px;
  height: 0px;
  /* Creating the arrow using borders */

  border: 4px solid transparent;
  border-left: 4px solid #d8d8d8;
}
/* The same arrow, but with a darker color, to create the shadow effect */
#cssmenu > ul .has-sub > a[data-v-1ba87392]::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 10px;
  width: 0px;
  height: 0px;
  /* Creating the arrow using borders */

  border: 4px solid transparent;
  border-left: 4px solid #000;
}
/* Changing the color of the arrow on hover */
#cssmenu > ul li > a[data-v-1ba87392]:hover::after,
#cssmenu > ul li:hover > a[data-v-1ba87392]::after {
  border-left: 4px solid #ffffff;
}
#cssmenu > ul li > a[data-v-1ba87392]:hover::before,
#cssmenu > ul li:hover > a[data-v-1ba87392]::before {
  border-left: 4px solid rgba(0, 0, 0, 0.3);
}
/* THE SUBMENUS */
#cssmenu > ul ul[data-v-1ba87392] {
  position: absolute;
  left: 100%;
  padding-left: 5px;
  top: -9999px;
  opacity: 0;
  width: 140px;
  /* The fade effect, created using an opacity transition */

  -webkit-transition: opacity 0.3s ease-in;
  transition: opacity 0.3s ease-in;
}
/* Showing the submenu when the user is hovering the parent link */
#cssmenu > ul li:hover > ul[data-v-1ba87392] {
  top: 0px;
  opacity: 1;
}

